@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,600);

body{
	font-family: 'Work Sans', sans-serif;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#243949), to(#517fa4));
  background: -webkit-linear-gradient(top, #243949 0%, #517fa4 100%);
  background: -o-linear-gradient(top, #243949 0%, #517fa4 100%);
  background: linear-gradient(to bottom, #243949 0%, #517fa4 100%);
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

@media screen and (orientation:landscape) {
  .container{
    -webkit-transform: scale(0.5);
        -ms-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transform-origin: top;
        -ms-transform-origin: top;
            transform-origin: top;

  }
body{
    max-width:96vw;
    max-height:44vh;
    overflow:hidden;
  }
  .header{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transform-origin: top;
        -ms-transform-origin: top;
            transform-origin: top;
    height: 14vh;
    max-width:99vw;
  }
}
